<template>
  <div class="commerceReturnDetail">
    <navigation-bar pageName="退货详情" right-text @onLeftClick="onClickLeft" />
    <div class="content-box">
      <!-- 商品信息 -->
      <div class="card">
        <div class="detailTypePanel">
          <span class="detailTypeName">{{orderInfo.shopName}}</span>
          <span class="orderStatus">{{orderInfo.billStatus}}</span>
        </div>
        <div class="solidLine"></div>
        <div class="detailGoodsPanel-name">
          <div class="orderGoods">{{orderInfo.spuName}}</div>
          <div class="orderPrice">￥{{orderInfo.skuPrice}}</div>
        </div>
        <div class="detailGoodsPanel-num">
          <div class="orderSpecs">{{orderInfo.attrValueContent}}</div>
          <div class="orderGoodsNum">x{{orderInfo.returnCount}}</div>
        </div>

        <div class="dashLine" style="margin: 10px 15px 0 15px;;"></div>
        <div class="detailInfoPanel">
          <div class="detailInfoItem">
            <span class="detailInfoTitle" style="color: #333333">配送费</span>
            <span class="infoDetai">￥{{orderInfo.deliverySku}}</span>
          </div>
          <div class="detailInfoItem">
            <span class="detailInfoTitle" style="color: #333333">实际支付</span>
            <span class="infoDetai" style="color: #F43D3D">￥{{orderInfo.realPaySkuTotal}}</span>
          </div>
        </div>
      </div>

      <!-- 退货信息 -->
      <div class="card">
        <div class="detailInfoPanel">
          <div class="detailInfoItem">
            <span class="detailInfoTitle">退货单号</span>
            <span class="infoDetai">{{orderInfo.returnBillNo}}</span>
          </div>
          <div class="detailInfoItem">
            <span class="detailInfoTitle">申请时间</span>
            <span class="infoDetai">{{orderInfo.returnTime}}</span>
          </div>
          <div class="detailInfoItem">
            <span class="detailInfoTitle">退货原因</span>
            <span class="reasonDetail">
              <div class="reasonDetailSub">
              {{orderInfo.returnReason}}
              </div></span>
          </div>
        </div>
        <div class="dashLine" v-if="orderInfo.returnPicUrl && orderInfo.returnPicUrl.length>0"></div>
        <!-- 照片滚动 -->
        <div class="returnImgPanel" v-if="orderInfo.returnPicUrl && orderInfo.returnPicUrl.length>0">
          <div class="img-box">
            <img class="returnImg" v-for="(item, index) in orderInfo.returnPicUrl" :key="index" :src="item" @click="preview(orderInfo.returnPicUrl,index)" />
          </div>
        </div>
      </div>

      <!-- 取件信息 -->
      <div class="card">
        <div class="detailTypePanel">
          <span class="detailTypeName">取件信息</span>
        </div>
        <div class="solidLine"></div>
        <div class="detailInfoPanel">
          <div class="detailInfoItem">
            <div class="detailInfoTitle">取件地址</div>
            <div class="infoDetai">{{orderInfo.returnAddr}}</div>
          </div>
          <div class="detailInfoItem">
            <span class="detailInfoTitle">联系人</span>
            <span class="infoDetai">
              {{orderInfo.returnName}}
              {{orderInfo.returnPhone}}
            </span>
          </div>
        </div>
      </div>
    </div>

    <!-- 根据产品经理需求输入，审核列表同步的情况下不允许操作，屏蔽审核详情操作按钮 -->
    <!-- 底部按钮 -->
    <!--<div class="receiveCommonPanel" v-if="orderInfo.billStatus=='待审核'">
      <div class="receiveBtn" @click="adoptReturn(orderInfo)">通过</div>
      <div class="rejectBtn" @click="rejectReturn(orderInfo)">驳回</div>
    </div>-->
  </div>
</template>

<script>
import {
  getOrderListService,
  setReturnStatusService
} from "../../services/commerceService.js";
import { store, storage } from "../../utils/store";
import { ImagePreview } from "vant";
import NavigationBar from "../title/NavigationBar";
export default {
  name: "commerceReturnDetail",
  components: { NavigationBar },
  data() {
    return {
      loading: false,
      finished: false,
      orderInfo: {},
      images: [],
    };
  },
  mounted() {
    this.orderInfo = this.$route.query.orderInfo;
  },
  methods: {
    adoptReturn(orderInfo) {
      this.setReturnStatus(orderInfo.returnBillNo, 1);
    },
    rejectReturn(orderInfo) {
      this.setReturnStatus(orderInfo.returnBillNo, 0);
    },
    setReturnStatus(returnBillNo, auditResult) {
      var requestData = {
        returnBillNo: returnBillNo,
        auditResult: auditResult
      };
      setReturnStatusService(requestData)
        .then(result => {
          //请求成功
          if (result.resultCode == "0") {
            store.get("returnDealFunction")(returnBillNo, auditResult ? "已完成" : "已驳回");
            this.orderInfo.billStatus = auditResult ? "已完成" : "已驳回"
            this.$toast("审批成功");
          } else {
            this.$toast("审批失败，请重试！");
          }
        })
        .catch(error => {
          this.$toast("网络异常，请稍后重试！");
        });
    },
    onClickLeft() {
      this.$router.back();
    },

    /**
     * 图片预览
     * @param
     * src    Array    图片url集合
     * index  Number   当前选中图片地址
     **/
    preview(src, index) {
      let urls = src.map(val => val);
      ImagePreview({ images: urls, startPosition: index });
    },
  }
};
</script>

<style>
.commerceReturnDetail {
  background-color: #f2f2f2;
  height: 100%;
  text-align: left;
  padding: 44px 0 64px 0;
  box-sizing: border-box;
}

.content-box {
  overflow: auto;
  width: 100%;
  height: 100%;
}

.card {
  padding-top: 1px;
  padding-bottom: 1px;
  box-shadow: 0 0 6px 0 rgba(219, 219, 219, 0.5);
  border-radius: 5px;
  overflow: hidden;
  margin: 10px 10px;
  background-color: #fff;
}

.card .dashLine {
  border: #e5e5e5 0.5px dashed;
}

/* 订单详情其它信息样式 */
.commerceReturnDetail .detailTypePanel {
  height: 40px;
  line-height: 40px;
  width: 100%;
}

.commerceReturnDetail .detailTypePanel .detailTypeName {
  font-size: 14px;
  font-weight: bold;
  color: #333333;
  letter-spacing: -0.04px;
  border-left: #00d8d1 3px solid;
  padding-left: 5px;
  margin: 10px 0 10px 15px;
}

.commerceReturnDetail .orderStatus {
  font-size: 14px;
  color: #333333;
  letter-spacing: -0.04px;
  text-align: right;
  float: right;
  margin-right: 15px;
}

/* 订单详情商品信息样式 */
.detailGoodsPanel {
  height: 20px;
  padding-top: 10px;
  display: flex;
}

.detailGoodsPanel-name {
  max-height: 40px;
  padding-top: 10px;
  display: flex;
}

.detailGoodsPanel-num {
  height: 20px;
  padding-top: 10px;
  display: flex;
}

.detailGoodsPanel-name .orderGoods {
  font-size: 14px;
  letter-spacing: -0.04px;
  margin-left: 15px;
  width: 250px;
  /*white-space: nowrap;*/
  overflow: hidden;
  text-overflow: ellipsis;
  text-overflow: -o-ellipsis-lastline;
  display: -webkit-box;
  -webkit-line-clamp: 2;
  line-clamp: 2;
  -webkit-box-orient: vertical;
  word-break: break-all;
}

.detailGoodsPanel-num .orderSpecs {
  font-size: 12px;
  color: #9a9a9a;
  letter-spacing: -0.04px;
  margin-left: 15px;
  width: auto;
  white-space: nowrap;
  overflow: hidden;
  text-overflow: ellipsis;
}

.detailGoodsPanel-num .orderGoodsNum {
  font-size: 12px;
  color: #9a9a9a;
  letter-spacing: -0.04px;
  margin-left: 15px;
  width: 20px;
}

.detailGoodsPanel-name .orderPrice {
  margin-right: 15px;
  font-size: 14px;
  color: #333333;
  flex-grow: 1;
  text-align: right;
}

/* 内容详情 */
.detailInfoPanel {
  width: 100%;
  padding-bottom: 10px;
}

.detailInfoPanel .detailInfoItem {
  /* height: 18px; */
  margin-top: 10px;
  margin-left: 15px;
  margin-right: 15px;
  display: flex;
  height: auto;
  word-break: break-word;
  flex-wrap: wrap;
  justify-content: space-between;
}

.detailInfoPanel .detailInfoTitle {
  font-size: 14px;
  color: #888888;
  letter-spacing: -0.04px;
  width: auto;
}

.detailInfoPanel .infoDetai {
  font-size: 14px;
  color: #333333;
  letter-spacing: -0.04px;
  text-align: right;
  float: right;
  flex-grow: 1;
}

.reasonDetail {
  font-size: 14px;
  color: #333333;
  letter-spacing: -0.04px;
  /*text-align: right;
  float: right;*/
  flex-grow: 1;
  max-width: 260px;
  overflow: hidden;
  text-overflow: ellipsis;
  text-align: left;
}

.reasonDetailSub {
  display: flex;
  justify-content: flex-end;
}

/* 发货方式tip */
