  position: absolute;
  right: 10px;
  top: 0;
  height: 40px;
  width: 40px;
  background-color: #f5a623;
  border-radius: 0 5px;
}

.commerceReturnDetail .SendTypeDelivery {
  position: absolute;
  right: 0;
