  line-height: 12px;
  width: 30px;
  height: 30px;
  text-align: center;
  margin: 10px 5px 0 5px;
  color: white;
} */

/* 收货按钮 */
.receiveCommonPanel {
  height: 60px;
  line-height: 60px;
  width: 100%;
  bottom: 0px;
  text-align: center;
  position: fixed;
  background-color: white;
  box-shadow: 0 -2px 7px 0 rgba(226, 226, 226, 0.5);
}

.receiveCommonPanel .receiveBtn {
  background: #00d8d1;
  border: 1px solid #00d8d1;
  box-shadow: 0 0 8px 0 rgba(0, 158, 161, 0.32);
  border-radius: 14.5px;
  height: 30px;
  line-height: 30px;
  width: 92px;
  color: white;
  text-align: center;
  font-size: 16px;
  float: right;
  margin-right: 15px;
  margin-top: 15px;
}
.receiveCommonPanel .rejectBtn {
  background: #fb5768;
  border: 1px solid #ff5050;
  box-shadow: 0 0 8px 0 rgba(197, 58, 72, 0.39);
  border-radius: 15px;
  height: 30px;
  line-height: 30px;
  width: 92px;
  color: white;
  text-align: center;
  font-size: 16px;
  float: right;
  margin-top: 13px;
  margin-right: 8px;
}
.commerceReturnDetail .returnImgPanel {
  padding: 15px;
}
.commerceReturnDetail .returnImgPanel .img-box {
  display: flex;
  justify-content: flex-start;
  align-items: center;
  overflow-x: auto;
  flex-wrap: nowrap;
  height: 80px;
}
.commerceReturnDetail .returnImgPanel img {
  width: 80px;
  height: 80px;
  margin-right: 15px;
}
</style>
